import {mainPageAPI, userAPI} from "../../api";
import {mainPageThunks} from "./mainPageThunks";
import {mainActions} from "../actions/mainActions";
import {stub} from "../../stub";
import {mainPageActions} from "../actions/mainPageActions";
import {courseThunks} from "./courseThunks";

export const mainThunks = {
    /**
     *        .
     *   App.js
     * @returns {(function(*, *): void)|*}
     */
    domain: () => dispatch => {
        /**    */
        userAPI.getUserData()
            .then(result => {
                if (result.AUTHORIZED) {
                    dispatch(mainActions.setUserData({
                        id: parseInt(result.USER_ID),
                        isAuthorised: result.AUTHORIZED,
                        isAdmin: result.IS_ADMIN,
                        isPayGS: result.PAY_GS,
                        isPayRadar: result.PAY_RADAR,
                        isPayRadarUSA: result.PAY_RADAR_USA
                    }));

                    dispatch(mainThunks.getMainData());
                } else {
                    console.log('  .');
                }
            })
            .catch(error => {
                console.log('   \n', error);
            });
    },

    /**
     *      
     * @returns {(function(*): void)|*}
     */
    getMainData: () => (dispatch, getState) => {
        const state = getState();
        const userId = state.mainReducer.user.id;

        Promise.all([
            {
                name: 'purchasedCoursesList',
                result: mainPageAPI.getPurchasedCoursesList(userId)
            }
        ])
            .then(responses => responses.forEach(response => {
                if (response.name === 'purchasedCoursesList') {
                    dispatch(mainThunks.getPurchasedCoursesList(response.result));
                }

                /* ------------------------------------ */

                /**     */
                dispatch(mainPageThunks.getAppSections());

                /**    ,    */
                dispatch(mainPageThunks.getCoursesList());

                /**     ,    */
                dispatch(mainPageThunks.getFilter());

                /**           */
                dispatch(mainPageThunks.getTopSectionSlides());

                /**     */
                dispatch(mainThunks.getArticles());

                /**       */
                dispatch(courseThunks.getTestQuestionTypes());
            }))
            .catch(error => {
                console.log('   .', error);
            });
    },

    /**
     *       
     *   
     * @returns {(function(*): void)|*}
     */
    getArticles: () => (dispatch) => {
        dispatch(mainActions.setArticles({ articles: stub.main.articles }));
    },

    /**
     *     ,
     *    -   isPurchased
     *    
     * @returns {(function(*, *): void)|*}
     */
    getPurchasedCoursesList: list => dispatch => {
        list.then(result => {
            const coursesIdsArray = [];

            // TODO   2  ID     
            coursesIdsArray.push(1);

            Object.keys(result).forEach(key => {
                if (result[key]) {
                    coursesIdsArray.push(key);
                }
            });

            dispatch(mainPageActions.setPayedCoursesList({ payedCoursesList: coursesIdsArray }));
        },
        error => {
            console.log('    .', error);
        });
    }
}
