import * as axios from "axios";

const apiVersion = 'v1';

const instance = axios.create({
    baseURL: `https://fin-plan.org/api/learning/${apiVersion}/`
});
const instanceUser = axios.create({
    baseURL: `https://fin-plan.org/api/techanalysis/`
});

export const userAPI = {
    /**
     * GET      
     */
    getUserData() {
        return instanceUser
            //.get(`/getUser/work/`)
            .get(`/getUser/debug/userId:1/authorized:true/isAdmin:true/payRadar:false/`)
            // .get(`/getUser/debug/userId:7307/authorized:true/isAdmin:true/payRadar:false/`)
            .then(response => response.data);
    }
};

export const mainPageAPI = {
    /**
     * GET      (    )
     * @returns {*}
     */
    getTagsLibrary() {
        return instance
            .get(`getTagsLibrary/`)
            .then(response => response.data);
    },

    /**
     * GET      ,   
     * @returns {*}
     */
    getCoursesList() {
        return instance
            .get(`getCoursesList/full/full`)
            .then(response => response.data);
    },

    /**
     * GET     ,   .
     *    id .
     * @param userId
     * @param courseId -  
     * @returns {*}
     */
    getPurchasedCoursesList(userId, courseId = null) {
        return instance
            .get(`getPayedItems/${userId}` + (courseId ? `/${courseId}` : ''))
            .then(response => response.data);
    }
};

export const coursePageAPI = {
    /**
     * GET       
     *
     * @param id -  ,     
     * @returns {*}
     */
    getCourseData(id) {
        return instance
            .get(`getCourseDetail/${id}/y`)
            .then(response => response.data);
    },

    /**
     * GET     .
     *    userId.
     *
     *     
     *     courseId
     *
     * @param userId
     * @param courseId
     * @returns {*}
     */
    getCourseStatistics(userId, courseId = null) {
        return instance
            .get(`getLearningStatistics/${userId}` + (courseId ? `/${courseId}` : ''))
            .then(response => response.data);
    },

    /**
     * GET        id
     *
     * @param userId
     * @param lessonId
     * @returns {*}
     */
    getLessonData(userId, lessonId) {
        return instance
            .get(`getLessonElement/full/${lessonId}/${userId}`)
            .then(response => response.data);
    },

    /**
     * POST     
     *  
     *
     * @param userId
     * @param lessonId
     * @param videoId
     * @param time -    hh:mm:ss (00:15:35)
     * @param isFinished -     
     * @returns {*}
     */
    setVideoProgress(userId, lessonId, videoId, time, isFinished = false) {
        return instance
            .post(`setVideoStatistics/${userId}/${lessonId}/${videoId}/${time}/${isFinished ? 'y' : 'n'}`);
    },

    /**
     * POST     
     *
     * @param userId
     * @param courseId
     * @param updateDate
     * @param lessonId
     * @param lessonStatus
     * @returns {*}
     */
    updateLessonStatus({userId, courseId, updateDate, lessonId, lessonStatus}) {
        return instance
            .put('setLearningStatistics/', {
                data: {
                    uid: userId,
                    courseId,
                    lastViewCourseDate: updateDate,
                    lessonId,
                    lessonStatus
                }
            },
            {
                headers: {
                    'Content-Type': 'text/plain'
                }
            });
    },

    /**
     * POST    
     *
     * @param userId
     * @param courseId
     * @param updateDate
     * @returns {*}
     */
    finishCourse({userId, courseId, updateDate}) {
        return instance
            .put('setLearningStatistics/', {
                data: {
                    uid: userId,
                    courseId,
                    lastViewCourseDate: updateDate,
                    courseSuccessEnd: true
                }
            },
            {
                headers: {
                    'Content-Type': 'text/plain'
                }
            });
    },

    /**
     * POST     
     *
     * @param userId
     * @param courseId
     * @param updateDate
     * @param testId
     * @param testStatus
     * @returns {*}
     */
    updateTestStatus({userId, courseId, updateDate, testId, testStatus}) {
        return instance
            .put('setLearningStatistics/', {
                data: {
                    uid: userId,
                    courseId,
                    lastViewCourseDate: updateDate,
                    testId,
                    testStatus
                }
            },
            {
                headers: {
                    'Content-Type': 'text/plain'
                }
            });
    },

    /**
     * GET          id  
     *
     * @returns {*}
     */
    getTestQuestionTypes() {
        return instance
            .get('getQuestionTypes/')
            .then(response => response.data);
    },

    /**
     * POST           
     *
     * @param uid
     * @param testId
     * @param questionId
     * @param questionType
     * @param answerId
     * @returns {*}
     */
    updateTestAnswer({uid, testId, questionId, questionType, answerId}) {
        return instance
            .post(`setUserTestAnswer/${uid}/${testId}/${questionId}/${questionType}/${answerId}`);
    },

    /**
     * POST           
     *
     * @param uid
     * @param testId
     * @param questionId
     * @param answerId
     * @returns {*}
     */
    deleteTestAnswer({uid, testId, questionId, answerId}) {
        return instance
            .post(`clearUserTestAnswer/${uid}/${testId}/${questionId}/${answerId}/`);
    },

    /**
     * POST          
     *
     * @param uid
     * @param testId
     * @returns {*}
     */
    deleteAllTestAnswers({uid, testId}) {
        console.log(testId)
        return instance
            .post(`clearAllUserTestAnswers/${uid}/${testId}/`)
    }
};
