import {mainPageActions} from "../actions/mainPageActions";
import {stub} from "../../stub";
import {mainPageAPI} from "../../api";
import {getBoolFromString} from "../../helpers";

export const mainPageThunks = {
    /**
     *           .
     *    isActive   " ".
     *        .   -   .
     * @returns {(function(*, *): void)|*}
     */
    getAppSections: () => (dispatch) => {
        mainPageAPI.getTagsLibrary()
            .then(data => {
                /**     */
                const appSections = [
                    ...Object.keys(data.category).map(elem => {
                        data.category[elem].isActive = false;
                        return data.category[elem]
                    }),
                    {
                        id: '-1',
                        code: 'purchases',
                        name: ' ',
                        isActive: false
                    }
                ];

                dispatch(mainPageActions.setAppSections({ appSections }));

                /* ------------------------------------ */

                /**      */
                const difficulty = Object.keys(data.difficulty).map(elem => data.difficulty[elem]);
                dispatch(mainPageActions.setDifficultyTypes(difficulty));
            })
            .catch(error => {
                console.log('       \n', error)
            });
    },

    /**
     *     ,   
     * @returns {(function(*))|*}
     */
    getCoursesList: () => (dispatch, getState) => {
        mainPageAPI.getCoursesList()
            .then(result => {
                const coursesList = result.map(course => {
                    return {
                        id: parseInt(course['ID']),
                        code: course['UF_XML_ID'],
                        name: course['UF_NAME'],
                        nameShort: course['UF_SHORT_NAME'],
                        description: course['UF_DESCRIPTION'],
                        img: `https://fin-plan.org/${course['UF_PICTURE']['SRC']}`,
                        link: course['UF_LINK'],
                        linkSecond: course['UF_AGAIN_LINK'],
                        duration: course['UF_DURATION'],
                        price: parseFloat(course['UF_PRICE']),
                        lessons: course['COUNT_LESSONS'],
                        tests: course['COUNT_TESTS'],
                        videos: course['COUNT_VIDEO'],
                        difficulty: course['UF_DIFFICULTY']['code'],
                        category: course['UF_CATEGORY']['code'],
                        isPopular: true, // TODO     
                        isPurchased: getState().mainPageReducer.payedCoursesList.includes(parseInt(course['ID'])),
                        // previewVideoUrl: course['UF_SHORT_VIDEO'],
                        previewVideoUrl: 'https://www.youtube.com/watch?v=668nUCeBHyY', // TODO    
                        //     
                        previewVideoPoster: `https://fin-plan.org/${course['UF_SHORT_VIDEO_PICTURE']}`,

                        isForGS: getBoolFromString(course['UF_ACCESS_GS_RUS']),
                        isForGSUSA: getBoolFromString(course['UF_ACCESS_GS_USA'])
                    }
                })

                dispatch(mainPageActions.setCoursesList({ coursesList }));
            })
            .catch(error => {
                console.log('   \n', error);
            });
    },

    /**
     *     ,   
     * @returns {(function(*))|*}
     */
    getFilter: () => (dispatch) => {
        dispatch(mainPageActions.setFilter({ filterList: stub.mainPage.filterList }));
    },

    /**
     *          
     * @returns {(function(*): void)|*}
     */
    getTopSectionSlides: () => (dispatch) => {
        dispatch(mainPageActions.setTopSectionSlides({ topSectionSlides: stub.mainPage.topSectionSlides }));
    }
}
