import * as actionTypes from "../actionTypes";

export const mainActions = {
    /**
     *          
     * @param params
     * @returns {{type: string, params}}
     */
    setArticles: params => ({
        type: actionTypes.SET_ARTICLES,
        params
    }),

    /**
     *     
     * @param params
     * @returns {{type: string, params}}
     */
    setUserData: params => ({
        type: actionTypes.SET_USER_DATA,
        params
    })
}
